import { OrganizationMembershipResource } from "@clerk/types"
import { Watchlist } from "@rp/common/api-types"
import { HEADER_HEIGHT } from "@rp/common/settings"
import { NoResultsIcon } from "@rp/react/common/assets"
import { WatchlistCardViewShowMoreList } from "@rp/react/web/common/watchlists"
import { Box, Typography } from "@rp/react/web/mui/core"
import { PublicWatchlists } from "../../WatchlistsListView"
import { WatchlistsModeView } from "../WatchlistsCardViewToggles"

type Props = {
  filterTerm: string
  containerEl: HTMLDivElement | null
  onCardClick: (watchlist: Watchlist) => void
  data: Array<Watchlist & { owner: OrganizationMembershipResource | undefined }> | undefined
  isLoading: boolean
  initialLines?: number
  modeView: WatchlistsModeView
}

export const FindWatchlists = ({
  filterTerm,
  containerEl,
  onCardClick,
  data,
  isLoading,
  initialLines = 3,
  modeView,
}: Props) => {
  const parts = filterTerm.toLowerCase().split(" ")
  const filtered = data?.filter(watchlist => {
    return parts.every(
      part =>
        watchlist.name.toLowerCase().includes(part) ||
        watchlist.owner?.publicUserData.firstName?.toLowerCase().includes(part) ||
        watchlist.owner?.publicUserData.lastName?.toLowerCase().includes(part) ||
        watchlist.global?.author?.name?.toLowerCase().includes(part),
    )
  })

  if (!isLoading && !filtered?.length) {
    return (
      <Box
        sx={{
          height: `calc(100dvh - ${HEADER_HEIGHT + 2}px)`,
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          flexDirection: "column",
        }}
      >
        <NoResultsIcon width={200} height={200} />
        <Typography sx={{ fontSize: 20, fontWeight: 700, mt: 3 }}>
          No watchlists found for your search
        </Typography>
        <Typography sx={{ color: "text.secondary", fontSize: 16 }}>
          We couldn't find any watchlist for your search. Try something different.
        </Typography>
      </Box>
    )
  }

  return (
    <Box
      sx={{
        my: "50px",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        width: "100%",
      }}
    >
      <Typography sx={{ fontSize: 32, fontWeight: 700 }}>
        {isLoading
          ? "Searching ..."
          : `${filtered?.length} watchlist${filtered?.length === 1 ? "" : "s"} found`}
      </Typography>
      <Typography sx={{ color: "text.secondary", fontSize: 16, my: 2 }}>
        {isLoading ? "Loading watchlists..." : "Showing watchlists that match your search term"}
      </Typography>
      {modeView === "card" && (
        <WatchlistCardViewShowMoreList
          onCardClick={onCardClick}
          containerEl={containerEl}
          data={filtered}
          initialLines={initialLines}
        />
      )}
      {modeView === "list" && <PublicWatchlists data={filtered} isLoading={!data} />}
    </Box>
  )
}
